import React from 'react';
import { Link } from 'react-router-dom';
import { TabBar } from 'antd-mobile';


const Goods = (props)=>{
    return(
        <div>
            <div style={{width:'100%'}}>
                <div style={{position:'absolute',width:'100%',overflow:'hidden'}}>
                    <img src='//gw.alicdn.com/bao/uploaded/i1/641946712/TB2GZ1uev9TBuNjy0FcXXbeiFXa_!!641946712.jpg_500x500q90.jpg_.webp'></img>
                </div>
                <div onClick={()=>props.history.push('/')}  style={{width:'40px',height:'40px',borderRadius:'50%',backgroundColor:'rgb(200,200,200,0.5)',position:'relative',left:'10px',top:'20px',display:'block'}}>
                    <i className='iconfont icon-fanhui' style={{color:'#fff',position:'relative',left:'10px',top:'10px'}}></i>
                </div>
            </div>
            <div style={{position:'fixed',width:'100%',height:'50px',bottom:'0',display:'flex',hmaxWidth:'768px',zIndex:'110',flexDirection:'row',padding:'0 0.2rem'}}>
                <div style={{width:'40%',height:'50px',display:'flex',borderTop:'1px solid #000'}}>
                    <div style={{flex:'1'}}>
                        <Link to='/dianpu'><i className='iconfont icon-dianpu' style={{color:'rgb(255, 133, 42)',fontSize:'30px',lineHeight:'50px',marginLeft:'10%'}}></i></Link>
                    </div>
                    <div style={{flex:'1'}}>
                        <Link to='/kefu' style={{color:'#000'}}><i className='iconfont icon-kefu' style={{color:'',fontSize:'30px',lineHeight:'50px',marginLeft:'10%'}}></i></Link>
                    </div>
                    <div style={{flex:'1'}}>
                        <Link to='/shoucang' style={{color:'#000'}}><i className='iconfont icon-shoucang' style={{color:'',fontSize:'30px',lineHeight:'50px',marginLeft:'10%'}}></i></Link>
                    </div>
                </div>
                <div style={{width:'60%',height:'50px',display:'flex',fontSize:'15px'}}>
                    <div style={{flex:'1',backgroundColor:'#ff9500',borderRadius:'5px',lineHeight:'50px',textAlign:'center'}}>
                        <Link to='/cart' style={{color:'#fff'}}><span>加入购物车</span></Link>
                    </div>
                    <div style={{flex:'1',backgroundColor:'#ff0036',borderRadius:'5px',lineHeight:'50px',textAlign:'center'}}>
                        <Link to='/order' style={{color:'#fff'}}><span>立即购买</span></Link>
                    </div>
                </div>
                

            </div>
        </div>
        

    )
}

export default Goods;