import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        // console.log(pathname.split('/'));
        if(pathname.split('/').length>2){
            return null;
        }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0,
                whiteSpace:'nowrap'
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="rgb(255, 133, 42)"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye1'></i>                      
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuche'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche1'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={  //选中时的图标
                            <i className='iconfont icon-dingdan1'></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/order' }
                        onPress={ () => {
                            this.props.history.push('/order')
                        } }
                        data-seed="logId2"
                    >    
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-yonghu'></i> }
                        selectedIcon={
                            <i className='iconfont icon-yonghu1'></i>
                        }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/myhome' }
                        onPress={ () => {
                            this.props.history.push('/myhome')
                        } }
                        data-seed="logId3"
                    > 
                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);