import React from 'react';
import { Carousel } from 'antd-mobile';

class Carou extends React.Component {
  state = {
    data: ['i2/96/O1CN014g0ADB1Ca0eWkU60Z_!!96-0-luban', 'i3/27/O1CN019CAYMI1C4PK6mp9qX_!!27-0-luban', 'i2/14/O1CN01FQBQC01BySAuFMDxB_!!14-0-luban'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['i2/96/O1CN014g0ADB1Ca0eWkU60Z_!!96-0-luban', 'i3/27/O1CN019CAYMI1C4PK6mp9qX_!!27-0-luban', 'i2/14/O1CN01FQBQC01BySAuFMDxB_!!14-0-luban'],
      });
    }, 100);
  }
  render() {
    return (
        <Carousel
          autoplay={true}
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="#"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`http://img.alicdn.com/imgextra/${val}.jpg`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
    );
  }
}


export default Carou;