import React from 'react';

const Input = (props)=>{
    return <div>
        <lable for="title" style={{float:'left',width:'100px',lineHeight:'50px',color:'#DDD'}}>TodoList</lable>
        <button onClick={()=>props.addTodo()} style={{float:'right',marginTop:'12px',width:'8%',height:'28px',marginLeft:'5px'}}>add</button>
        <input type='text' placeholder='添加Todo' value={props.data} onChange={(e)=>props.handleChange(e)} id='inp' style={{float:'right',width:'60%',height:'24px',marginTop:'12px',textIndent:'10px',borderRadius:'5px'}}>
        </input>
    </div>
}

export default Input;